export default {
	nmae: "postusercard",
	data() {
		return {
			newUser: {
				name: "",
				kana: "",
				goal: "",
			},
		};
	},
	methods: {
		postUser() {
			this.$store.dispatch("postUserAction", this.newUser);
			this.newUser = [];
		},
		cancel() {
			this.$emit("cancel");
			this.newUser = [];
		},
	},
	computed: {
		getNewUsersId() {
			return this.$store.getters.getUsers.length + 1;
		},
	},
};
