import LoginTab from "./LoginTab";
import SigninTab from "./SigninTab";

export default {
  data() {
    return {
      tab: null,
      dialog: false,
      activeTab: 1,
      childTab: 0,
      parentTrainer: {
        name: "",
        kana: "",
        password: "",
        occupation: "",
      },
      parentUser: {
        name: "",
        kana: "",
        password: "",
      },
    };
  },
  methods: {
    childTabchoice(e) {
      this.childTab = e;
    },
    closeDialog() {
      this.dialog = false;
    },
    postNewTrainerOrUser() {
      switch (this.childTab) {
        case 1:
          this.$store.dispatch("postTrainerAction", this.parentTrainer);
          break;
        case 2:
          this.$store.dispatch("postUserAction", this.parentUser);
          break;
        default:
          console.log("タグ選択時にエラー有り");
      }
      this.parentTrainer = {
        name: "",
        kana: "",
        password: "",
        occupation: "",
      };
      this.parentUser = {
        name: "",
        kana: "",
        password: "",
      };
    },
    setChildTrainer(e) {
      this.parentTrainer = e;
    },
    setChildUser(e) {
      this.parentUser = e;
    },
  },
  components: {
    LoginTab,
    SigninTab,
  },
};
