import AbilityChart from "./AbilityChart";
import UserCalendar from "./UserCalendar";
import BaseMenu from "./BaseMenu";
export default {
	name: "usercard",
	data() {
		return {
			tab: null,
			menu: null,
		};
	},
	props: {
		id: Number,
		name: String,
		kana: String,
		goal: String,
		baseMenu: Array,
		comment: String,
		ability: Object,
	},
	components: {
		AbilityChart,
		UserCalendar,
		BaseMenu,
	},
	methods: {
		deleteUserById(id) {
			this.$store.dispatch("deleteUserByIdAction", { id });
		},
		postUser() {
			this.$store.dispatch("postUserAction", this.newUser);
		},
	},
};
